import { Component, Prop, Vue, Watch } from "vue-property-decorator";


import { PlatformInfo } from "@/dto/PlatformInfo";
import InvoiceItem from "@/dto/InvoiceItem";
import moment from 'moment'
import { format, parseISO } from 'date-fns'
import AddressPopUp from "@/components/AddressPopUp/AddressPopUp.vue";
import Invoice from "@/dto/Invoice";
import { FirestoreService } from "@/services/FirestoreService";
import { GstConfiguration } from "@/dto/GstConfiguration";
import { auth } from "@/firebaseInit";
import { AuditFields } from "@/dto/AuditFields";
import Address from "@/dto/Address";
import axios from "axios";
import { InvoiceTemplate } from "@/templates/InvoiceTemplate";
import Swal from "sweetalert2";

@Component({
  components: {
    AddressPopUp
  }
})
export default class Invoicing extends Vue {
  invoice = new Invoice();

  isShippingAddress = false;
  edit = false;
  printDialog = false;
  loading = false;
  label = "Add Platform";
  taxRates = [0, 0.1, 0.25, 1, 1.5, 3, 5, 7.5, 12, 18, 28];
  itemTypes = ['Goods', 'Services']
  invoiceDateMenu = false;
  dueDateMenu = false;
  billingAddressFlag = false;
  billEdit = false;
  invoiceDate = format(parseISO(new Date().toISOString()), 'yyyy-MM-dd');
  dueDate = format(parseISO(new Date().toISOString()), 'yyyy-MM-dd')
  shippingAddressEdit = false;
  billingLabel = "Billing Address";
  gstDetailLoading = false;
  isTaxInclusive = true;
  isGST = true;
  rateLabel = "Rate Inc Tax(Rs.)"
  gstConfig = new GstConfiguration();
  previewData: any = '';
  receiptTypes: any[] = [
    'Original for Recipient', 'Duplicate for Transporter', 'Triplicate for Supplier'];
  get invoiceDateFormatted() {
    return this.invoiceDate ? moment(this.invoiceDate).format('DD/MM/YYYY') : '';
  }

  get dueDateFormatted() {
    return this.dueDate ? moment(this.dueDate).format('DD/MM/YYYY') : '';
  }

  @Watch('isTaxInclusive')
  async onPropertyChanged(value: boolean, oldValue: boolean) {
    if (value !== oldValue && value === false) {
      this.rateLabel = "Rate(Rs.)"
      for (const item of this.invoice.invoiceItems) {
        item.isTaxInclusive = false;
      }
    } else if (value !== oldValue && value === true) {
      this.rateLabel = "Rate Inc Tax(Rs.)"
      for (const item of this.invoice.invoiceItems) {
        item.isTaxInclusive = true;
      }
    }

  }
  @Watch('invoice.billingAddress.state', { deep: true, immediate: true })
  async onBillingAddressChanged(value: string, oldValue: string) {
    console.log("invoice.billingAddress.state", this.gstConfig.gstAddress.state)
    console.log("value", value)
    if (value && this.gstConfig.gstAddress.state && this.gstConfig.gstAddress.state !== value) {
      for (const item of this.invoice.invoiceItems) {
        item.isIgst = true;
      }
    } else {
      for (const item of this.invoice.invoiceItems) {
        item.isIgst = false;
      }
    }
    this.invoice.placeOfSupply = value;

  }
  setCustName() {
    this.invoice.billingAddress.name = this.invoice.customerName;
  }
  @Watch('invoice.shippingAddress.state', { deep: true, immediate: true })
  async onShippingddressChanged(value: string, oldValue: string) {
    if (oldValue !== value && value !== '') {
      this.invoice.placeOfDelivery = value;
    }
  }
  async mounted() {
    console.log(this.$route.query.id)
    if (this.$route.query.id) {
      this.edit = true;
      try {
        const invoiceData = await FirestoreService.getSingleDocument('invoices', this.$route.query.id as string, Invoice.converter);
        this.invoice = invoiceData.data();
        //this.invoiceDate = format(moment(this.invoice.invoiceDate, 'DD/MM/YYYY').format('yyyy-MM-dd'), 'yyyy-MM-dd');
        this.invoiceDate = moment(this.invoice.invoiceDate, 'DD/MM/YYYY').format('YYYY-MM-DD');
        this.dueDate = moment(this.invoice.dueDate, 'DD/MM/YYYY').format('YYYY-MM-DD');
        this.setShippingAddress();
        this.isTaxInclusive = this.invoice.isTaxInclusive;
      } catch (error) {
        Vue.toasted.show(error.message, { icon: "cross", type: "error" });
      }
    } else {
      this.edit = false;
      this.invoice = new Invoice();
      const invoiceItem: InvoiceItem = new InvoiceItem();
      this.invoice.invoiceItems.push(invoiceItem);
    }

    this.label = this.edit ? "Edit Invoice" : "New Invoice";
    await this.getGstDetails();
  }
  async validateForm() {
    if ((this.$refs.addInvoiceForm as Vue & { validate: () => boolean }).validate()) {
      if (this.edit) {
        await this.updateInvoice();

      } else {
        await this.addInvoice();
      }
    }
  }
  setShippingAddress() {
    if (this.invoice.sameAddress) {
      this.invoice.shippingAddress = this.invoice.billingAddress;
      this.shippingAddressEdit = true;
    } else {
      this.shippingAddressEdit = false;
      this.invoice.shippingAddress = new Address();
    }
  }
  printItem() {
    const data = InvoiceTemplate.getMessage(this.invoice);
    this.previewData = URL.createObjectURL(new Blob([data], { type: 'text/html' }));
    const newWindow = window.open(this.previewData, '_blank');
    if (newWindow) {
      newWindow.onload = () => {
        newWindow.document.getElementsByTagName('html')[0].style.backgroundColor = 'whiteSmoke';
      };
    }
  }
  async addInvoice() {
    try {
      this.loading = true;
      this.invoice.dueDate = this.dueDateFormatted;
      this.invoice.invoiceDate = this.invoiceDateFormatted;
      const dateNow = new Date();
      const auditfield: AuditFields = {
        createdBy: { uid: auth.currentUser?.uid, name: auth.currentUser?.displayName, email: auth.currentUser?.email },
        createdOn: dateNow.getTime()
      }
      this.invoice.auditTrail = auditfield;

      await this.setItemCosts();
      const dbResp = await FirestoreService.addDocumentWithId('invoices', this.invoice.invoiceNumber, this.invoice, Invoice.converter);
      Vue.toasted.show('Invoice Added Successfully', { icon: 'face', type: 'success' });
      await this.showAfterSaveDialog();

    }
    catch (error) {
      console.log(error)
      Vue.toasted.show(error.message, { icon: 'cross', type: 'error' });

    } finally {
      this.loading = false;
    }
  }
  async updateInvoice() {
    try {
      this.loading = true;
      this.invoice.dueDate = this.dueDateFormatted;
      this.invoice.invoiceDate = this.invoiceDateFormatted;
      const dateNow = new Date();
      const auditfield: AuditFields = {
        createdOn: this.invoice.auditTrail.createdOn,
        createdBy: this.invoice.auditTrail.createdBy,
        updatedBy: { uid: auth.currentUser?.uid, name: auth.currentUser?.displayName, email: auth.currentUser?.email },
        updatedOn: dateNow.getTime()
      }
      this.invoice.auditTrail = auditfield;

      await this.setItemCosts();
      const dbResp = await FirestoreService.setOrMergeDocument('invoices', this.invoice.invoiceNumber, this.invoice, Invoice.converter);
      Vue.toasted.show('Invoice updated Successfully', { icon: 'face', type: 'success' });

    }
    catch (error) {
      console.log(error)
      Vue.toasted.show(error.message, { icon: 'cross', type: 'error' });

    } finally {
      this.loading = false;
    }
  }
  async setItemCosts() {
    for (const item of this.invoice.invoiceItems) {
      item.sgst = item.sgstComputed;
      item.igst = item.igstComputed;
      item.cgst = item.cgstComputed;
      item.total = item.totalComputed;
      item.totalTax = item.totalTaxComputed;
      item.taxableValue = item.taxableValueComputed;
    }
    this.invoice.totalTax = this.invoice.totalTaxComputed;
    this.invoice.totalCost = this.invoice.totalCostComputed;
  }
  async getItemCosts() {
    for (const item of this.invoice.invoiceItems) {
      item.sgstComputed = item.sgst;
      item.igstComputed = item.igst;
      item.cgstComputed = item.cgst;
      item.totalComputed = item.total;
      item.totalTaxComputed = item.totalTax;
      item.taxableValueComputed = item.taxableValue;
    }
    this.invoice.totalTaxComputed = this.invoice.totalTax;
    this.invoice.totalCostComputed = this.invoice.totalCost;
  }
  async resetForm() {
    (this.$refs.addInvoiceForm as Vue & { resetValidation: () => boolean }).resetValidation()

    this.invoice = new Invoice();
    const invoiceItem: InvoiceItem = new InvoiceItem();
    this.invoice.invoiceItems.push(invoiceItem);
    if (this.gstConfig.gstNumber) {
      this.invoice.gstConfig = this.gstConfig;
    } else {
      await this.getGstDetails();
    }
    this.isTaxInclusive = true;
    this.isGST = true;
  }
  editBillingAddress(item: PlatformInfo) {
    this.billingAddressFlag = true;
    if (this.invoice.billingAddress.stateCode !== '') {
      this.billEdit = true;
    } else {
      this.billEdit = false;
    }
    this.isShippingAddress = true;
    this.billingLabel = "Billing Address";
    this.$store.dispatch('setAddressPopUpFlag', true);
  }
  editShippingAddress(item: PlatformInfo) {
    this.billingAddressFlag = false;
    if (this.invoice.shippingAddress.stateCode !== '') {
      this.billEdit = true;
    } else {
      this.billEdit = false;
    }
    this.isShippingAddress = false;

    this.billingLabel = "Shipping Address";
    this.$store.dispatch('setshippingAddressPopUpFlag', true);
  }






  cancelEdit() {
    this.$store.dispatch('setInvoicePanelFlag', false);
  }

  headers: any = [
    { text: '#', value: 'srNo', sortable: false, fixed: true, divider: true, width: '20px', },
    { text: 'Item Description', value: 'itemDesc', fixed: true, width: '300px', divider: true },
    {
      text: 'Item Type', value: 'itemType', divider: true, width: '150px',

    },
    { text: 'Serial No.', value: 'serialNumber', sortable: false, divider: true, width: '200px', },
    { text: 'HSN / SAC', value: 'hsn', sortable: false, divider: true, width: '200px', },

    { text: 'Qty', value: 'quantity', sortable: false, divider: true, width: '80px', align: 'center' },


    { text: this.rateLabel, value: 'mrp', divider: true, sortable: false, width: '200px', },
    { text: 'Discount Rate', value: 'discountValue', divider: true, sortable: false, width: '200px', },
    { text: 'Discount (%)', value: 'discount', divider: true, sortable: false, width: '110px', },
    { text: 'Net Amount', value: 'netAmount', sortable: false, divider: true, width: '120px' },

    { text: 'Taxable Amount', value: 'taxableValueComputed', sortable: false, divider: true, width: '120px' },

    { text: 'Taxable Rate', value: 'taxableRate', sortable: false, divider: true, width: '120px' },

    { text: 'CGST', value: 'cgstComputed', sortable: false, divider: true, width: '100px', align: 'center', cellClass: 'grey lighten-3' },
    { text: 'SGST', value: 'sgstComputed', sortable: false, divider: true, width: '100px', align: 'center', cellClass: 'grey lighten-3' },
    { text: 'IGST', value: 'igstComputed', sortable: false, divider: true, width: '100px', align: 'center', cellClass: 'grey lighten-3' },
    { text: 'TOTAL', value: 'totalComputed', sortable: false, fixed: true, width: '150px', divider: true, align: 'end' },
    { text: 'Actions', value: 'actions', sortable: false },

  ]

  addItem() {
    const invoiceItem: InvoiceItem = new InvoiceItem();
    this.invoice.itemCount += 1;
    invoiceItem.srNo = this.invoice.itemCount;
    if (this.invoice.billingAddress.state !== '' && this.invoice.billingAddress.state !== this.gstConfig.gstAddress.state) {
      invoiceItem.isIgst = true;
    }

    this.invoice.invoiceItems.push(invoiceItem);
  }
  deleteTableItem(invoiceItem: InvoiceItem) {
    this.invoice.itemCount -= 1;
    this.invoice.invoiceItems.splice(this.invoice.invoiceItems.indexOf(invoiceItem), 1)
    this.invoice.invoiceItems = [...this.invoice.invoiceItems]
    for (const [index, item] of this.invoice.invoiceItems.entries()) {
      item.srNo = index + 1;
    }
  }
  async getGstDetails() {
    try {
      this.gstDetailLoading = true;
      const snapshot = await FirestoreService.getDocument('setting', 'GSTInfo', GstConfiguration.converter);
      if (snapshot !== null) {
        this.invoice.gstConfig = snapshot.data() as GstConfiguration;
        this.gstConfig = snapshot.data() as GstConfiguration;
        if (!this.edit) {
          this.invoice.placeOfSupply = this.gstConfig.gstAddress.state;
          this.invoice.placeOfDelivery = this.gstConfig.gstAddress.state;
        }
      } else {
        Vue.toasted.show('No Saved Details found, Please set GST details first', { icon: 'face', type: 'error', duration: 5000 });
      }


    } catch (error) {
      Vue.toasted.show('Error fetching GST Details', { icon: 'face', type: 'error' });
      console.log(error)

    }
    finally {
      this.gstDetailLoading = false;
    }

  }
  openPrintDialog() {
    this.printDialog = true;
  }
  closePrintDialog() {
    this.printDialog = false;
  }
  async showAfterSaveDialog() {
    try {
      const res = await Swal.fire({
        title: 'Invoice Saved',
        text: "Do you want to print ",
        icon: 'success',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: 'Yes',
        cancelButtonText: 'No'
      });
      if (res.isConfirmed) {
        this.edit = true;
        this.openPrintDialog();
      } else {
        this.resetForm();
      }
    } catch (error) {
      Vue.toasted.show(error.message, { icon: 'face', type: 'error' });

    }

  }
}
