import { classToPlain, plainToClass } from "class-transformer";
import { v4 as uuidv4 } from 'uuid';

export default class InvoiceItem {
    srNo: number;
    id: string;
    itemDesc: string;
    itemType: string;
    hsn: string;
    quantity: number;
    mrp: number;
    taxableRate: number;
    isIgst = false;
    isTaxInclusive = true;
    cgst: number;
    sgst: number;
    igst: number;
    total: number;
    totalTax: number;
    taxableValue: number;
    serialNumber: string;
    discount: number;
    discountValue: number;

    constructor() {
        this.id = uuidv4();
        this.itemDesc = '';
        this.itemType = 'Goods';
        this.hsn = '';
        this.quantity = 1;
        this.mrp = 0;
        this.taxableRate = 18;
        this.srNo = 1;
        this.igst = 0;
        this.cgst = 0;
        this.sgst = 0;
        this.total = 0;
        this.totalTax = 0;
        this.taxableValue = 0;
        this.serialNumber = "";
        this.discount = 0;
        this.discountValue = 0;
    }

    get cgstComputed() {
        if (this.isIgst === false) {
            return this.totalTaxComputed / 2;
        }
        else {
            return 0;
        }
    }
    set cgstComputed(cgst: number) {
        this.cgstComputed = cgst;
    }
    get sgstComputed() {
        if (this.isIgst === false) {
            return this.totalTaxComputed / 2;


        }
        else {
            return 0;
        }
    }
    set sgstComputed(sgst: number) {
        this.sgstComputed = sgst;
    }
    get igstComputed() {
        if (this.isIgst === true) {
            return this.totalTaxComputed;
        }
        else {
            return 0;
        }
    }
    set igstComputed(igst: number) {
        this.igstComputed = igst;
    }
    get totalComputed() {
        if (!this.isTaxInclusive) {
            const val = this.totalMrp + this.totalTaxComputed;
            return Math.round((val + Number.EPSILON) * 100) / 100;
        } else {
            const val = this.totalMrp;
            return Math.round((val + Number.EPSILON) * 100) / 100;
        }
    }
    set totalComputed(total: number) {
        this.totalComputed = total;
    }
    get totalMrp() {
        if (this.discountValue > 0) {
            this.discount = Math.round(((100 - ((this.discountValue / this.mrp) * 100)) + Number.EPSILON) * 100) / 100;
            // this.discount = Math.round((this.discount + Number.EPSILON) * 100) / 100;
            return this.discountAmount;
        } else {
            this.discount = 0;
            return this.netAmount;
        }
    }
    get netAmount() {
        return this.mrp * this.quantity;
    }
    set netAmount(value: number) {
        this.netAmount = value;
    }
    get discountAmount() {
        return this.discountValue * this.quantity;
    }

    get totalTaxComputed() {
        if (this.isTaxInclusive) {
            const val = (this.totalMrp * this.taxableRate) / (100 + this.taxableRate);
            return Math.round((val + Number.EPSILON) * 100) / 100;
        } else {
            const taxRate = this.taxableRate / 100;
            const val = (this.totalMrp * taxRate);
            return Math.round((val + Number.EPSILON) * 100) / 100;
        }
    }

    set totalTaxComputed(totalTax: number) {
        this.totalTaxComputed = totalTax;
    }
    get taxableValueComputed() {
        if (this.isTaxInclusive) {
            const val = this.totalMrp - this.totalTaxComputed;
            return Math.round((val + Number.EPSILON) * 100) / 100;
        } else {
            const val = this.totalMrp;
            return Math.round((val + Number.EPSILON) * 100) / 100;
        }
    }
    set taxableValueComputed(taxableValue: number) {
        this.taxableValueComputed = taxableValue;
    }
    get mrpTaxComputed() {
        if (this.isTaxInclusive) {
            const val = (this.mrp * this.taxableRate) / (100 + this.taxableRate);
            return Math.round((val + Number.EPSILON) * 100) / 100;
        } else {
            const taxRate = this.taxableRate / 100;
            const val = (this.mrp * taxRate);
            return Math.round((val + Number.EPSILON) * 100) / 100;
        }
    }
    get individualTaxableValueComputed() {
        if (this.isTaxInclusive) {
            const val = this.mrp - this.mrpTaxComputed;
            return Math.round((val + Number.EPSILON) * 100) / 100;
        } else {
            const val = this.mrp;
            return Math.round((val + Number.EPSILON) * 100) / 100;
        }
    }
    get individualTaxableValueComputedForQty() {
        const val = this.quantity * this.individualTaxableValueComputed
        return Math.round((val + Number.EPSILON) * 100) / 100;

    }

}