import "es6-shim";
import "reflect-metadata";
import { classToPlain, plainToClass, Type } from "class-transformer";
import Address from "./Address";
import { GstConfiguration } from "./GstConfiguration";
import InvoiceItem from "./InvoiceItem";
import { AuditFields } from "./AuditFields";
import { v4 as uuidv4 } from "uuid";
import InventoryItem from "./InventoryItem";

export default class ItemsCheckout {
  @Type(() => InventoryItem)
  inventoryItems: InventoryItem[] = [];
  invoiceDate: string;
  quantity = 0;
  id: string;
  auditTrail!: AuditFields;
  itemCount = 0;
  orderId = "";
  constructor() {
    this.invoiceDate = "";

    this.id = uuidv4();
  }
  get totalCostComputed() {
    let total = 0;
    for (const item of this.inventoryItems) {
      total += item.total;
    }
    return total;
  }

  set totalCostComputed(totalCost: number) {
    this.totalCostComputed = totalCost;
  }

  static converter = {
    toFirestore(expense: ItemsCheckout): firebase.firestore.DocumentData {
      return classToPlain(expense);
    },
    fromFirestore(
      snapshot: firebase.firestore.QueryDocumentSnapshot,
      options: firebase.firestore.SnapshotOptions
    ): ItemsCheckout {
      const data = snapshot.data(options)!;
      return plainToClass(ItemsCheckout, data);
    },
  };
}
