import 'es6-shim';
import 'reflect-metadata';
import { classToPlain, plainToClass, Type } from 'class-transformer';
import Address from "./Address";
import { GstConfiguration } from "./GstConfiguration";
import InvoiceItem from "./InvoiceItem";
import { AuditFields } from "./AuditFields";
import { v4 as uuidv4 } from 'uuid';

export default class Invoice {
    @Type(() => InvoiceItem)
    invoiceItems: InvoiceItem[] = [];
    billingAddress: Address;
    shippingAddress: Address;
    invoiceNumber: string;
    invoiceDate: string;
    dueDate: string;
    referenceNumber: string;
    customerName: string;
    customerGSTIN: string;
    placeOfSupply: string;
    isTaxInclusive = true;
    isGst = true;
    gstConfig: GstConfiguration;
    auditTrail!: AuditFields;
    totalCost: number;
    totalTax: number;
    placeOfDelivery: string;
    id: string;
    sameAddress = false;
    itemCount = 1;
    quantity=0;
    receiptHeader = 'Original for Recipient';

    constructor() {
        this.billingAddress = new Address();
        this.shippingAddress = new Address();
        this.invoiceNumber = '';
        this.invoiceDate = '';
        this.dueDate = '';
        this.referenceNumber = '';
        this.customerName = '';
        this.placeOfSupply = '';
        this.customerGSTIN = '';
        this.invoiceNumber = '';
        this.invoiceNumber = '';
        this.invoiceNumber = '';
        this.gstConfig = new GstConfiguration();
        this.totalCost = 0;
        this.totalTax = 0;
        this.placeOfDelivery = "";
        this.id = uuidv4();
    }
    get totalCostComputed() {
        let total = 0;
        for (const item of this.invoiceItems) {
            total += item.totalComputed;
        }
        return Math.round((total + Number.EPSILON) * 100) / 100;
    }

    set totalCostComputed(totalCost: number) {
        this.totalCostComputed = totalCost;
    }
    get totalTaxComputed() {
        let total = 0;
        for (const item of this.invoiceItems) {
            total += item.totalTaxComputed;
        }
        return Math.round((total + Number.EPSILON) * 100) / 100;
    }
    set totalTaxComputed(totalTax: number) {
        this.totalTaxComputed = totalTax;
    }

    get totalIndividualTaxableValueComputedForQty() {
        let total = 0;
        for (const item of this.invoiceItems) {
            total += item.individualTaxableValueComputedForQty;
        }
        return total;
    }
    get totalQuantity() {
        let total = 0;
        for (const item of this.invoiceItems) {
            total += item.quantity;
        }
        return total;
    }
    get isIGST() {
        let igst = false;
        for (const item of this.invoiceItems) {
            igst= item.isIgst;
        }
        return igst;
    }
    get totalIgst() {
        let total = 0;
        for (const item of this.invoiceItems) {
            total+= item.igstComputed;
        }
        return total;
    }
    get totalCgst() {
        let total = 0;
        for (const item of this.invoiceItems) {
            total+= item.cgstComputed;
        }
        return total;
    }
    get totalSgst() {
        let total = 0;
        for (const item of this.invoiceItems) {
            total+= item.sgstComputed;
        }
        return total;
    }
    static converter = {
        toFirestore(expense: Invoice): firebase.firestore.DocumentData {
            return classToPlain(expense);
        },
        fromFirestore(
            snapshot: firebase.firestore.QueryDocumentSnapshot,
            options: firebase.firestore.SnapshotOptions
        ): Invoice {
            const data = snapshot.data(options)!;
            return plainToClass(Invoice, data);
        }
    };
}