  async updateQuantity(indexItem: InventoryItem) {
    console.log(indexItem);
    const currItem = this.itemsCheckout.inventoryItems.find(
      (x) => x.itemCode == indexItem.itemCode
    );
    if (currItem) {
      if (currItem.purchaseQuantity > currItem.availableQuantity) {
        currItem.purchaseQuantity -= 1;
        Swal.fire({
          title: `Item ${currItem.itemCode} max quanity reached`,
          text: `${currItem.itemTag} is only available in ${currItem.availableQuantity} quantity.`,
          icon: "error",
          confirmButtonText: "ok",
        });
      }
    }
  }
  async searchItem() {
    try {
      this.searchItemLoading = true;
      const snapshot = await FirestoreService.getDocument(
        "Inventory",
        this.currentItemcode,
        InventoryItem.converter
      );
      if (snapshot !== null) {
        const fetchedItem = snapshot.data() as InventoryItem;
        if (fetchedItem.availableQuantity == 0 || !fetchedItem.isInStock) {
          const res = await Swal.fire({
            title: `Item ${fetchedItem.itemCode} not in stock`,
            text: `${fetchedItem.itemTag} is not in stock.`,
            icon: "error",
            confirmButtonText: "ok",
          });
        } else {
          const currItem = this.itemsCheckout.inventoryItems.find(
            (x) => x.itemCode == fetchedItem.itemCode
          );
          if (currItem) {
            if (
              currItem.purchaseQuantity + 1 <=
              fetchedItem.availableQuantity
            ) {
              currItem.purchaseQuantity += 1;
              this.currentItemcode = "";
            } else {
              const res = await Swal.fire({
                title: `Item ${fetchedItem.itemCode} max quanity reached`,
                text: `${fetchedItem.itemTag} is only available in ${fetchedItem.availableQuantity} quantity.`,
                icon: "error",
                confirmButtonText: "ok",
              });
            }
          } else {
            this.itemsCheckout.itemCount = this.itemsCheckout.itemCount + 1;
            fetchedItem.srNo = this.itemsCheckout.itemCount;
            this.itemsCheckout.inventoryItems.push(fetchedItem);
            this.currentItemcode = "";
          }
        }
      } else {
        Vue.toasted.show("Item not found", {
          icon: "face",
          type: "error",
          duration: 5000,
        });
      }
    } catch (error) {
      Vue.toasted.show("Error Fetching details", {
        icon: "face",
        type: "error",
      });
      console.log(error);
    } finally {
      this.searchItemLoading = false;
    }
  }
  addItem() {
    const invoiceItem: InventoryItem = new InventoryItem();
    this.itemsCheckout.itemCount += 1;
    invoiceItem.srNo = this.itemsCheckout.itemCount;

    this.itemsCheckout.inventoryItems.push(invoiceItem);
  }
  deleteTableItem(invoiceItem: InventoryItem) {
    this.itemsCheckout.itemCount -= 1;
    this.itemsCheckout.inventoryItems.splice(
      this.itemsCheckout.inventoryItems.indexOf(invoiceItem),
      1
    );
    this.itemsCheckout.inventoryItems = [...this.itemsCheckout.inventoryItems];
    for (const [index, item] of this.itemsCheckout.inventoryItems.entries()) {
      item.srNo = index + 1;
    }
  }

