import 'es6-shim';
import 'reflect-metadata';
import Address from "./Address";
import { AuditFields } from "./AuditFields";
import { classToPlain, plainToClass, Type } from 'class-transformer';
import BankDetails from './BankDetails';

export class RetailGstConfiguration {
    gstNumber: string;
    panNumber: string;
    name: string;
    description: string;
    @Type(() => Address)
    gstAddress: Address;
    logoUrl: string;
    authSignUrl: string;
    auditTrail!: AuditFields;
    termsAndConditions: string;
    emailId: string;
    mobileNumber: string;
    bankDetails: BankDetails;
    constructor() {
        this.gstAddress = new Address();
        this.gstNumber = '';
        this.panNumber = '';
        this.name = '';
        this.description = '';
        this.logoUrl = '';
        this.authSignUrl = '';
        this.termsAndConditions = '';
        this.emailId = "";
        this.mobileNumber = "";
        this.bankDetails =new BankDetails();
    }
    static converter = {
        toFirestore(expense: RetailGstConfiguration): firebase.firestore.DocumentData {
            return classToPlain(expense);
        },
        fromFirestore(
            snapshot: firebase.firestore.QueryDocumentSnapshot,
            options: firebase.firestore.SnapshotOptions
        ): RetailGstConfiguration {
            const data = snapshot.data(options)!;
            return plainToClass(RetailGstConfiguration, data);
        }
    };
}